import React from 'react'
import TemplateEscuro from './TemplateEscuro';

export default function Creditos({mobile}) { 
    const content =
    <div style={{ textAlign: "center", display:mobile?"block":"flex", fontSize: mobile ? "0.9em" : "1em", marginTop: mobile ? "2em" : "-2em" }}>        
        <div style={{margin:mobile ? "0":"5em 2em 0em 5em"}}>
            <div style={{ fontFamily: "Luckiest Guy" }}>COORDENAO E SUPERVISAO GERAL</div>
            <div>Leila Maria Beltramini</div>
            <div>Nelma R. S. Bossolan</div>
            <br></br>
            <div style={{fontFamily:"Luckiest Guy"}}>EDUCADORA</div>
            <div>Gislaine Costa dos Santos</div>
            <br></br>
            <div style={{fontFamily:"Luckiest Guy"}}>CONSULTORIA E ORIENTAO DE TI</div>
            <div>Claudinei Caetano de Souza (verso 1.0)</div>
            <div>Simone Ferreira Souza (verso 2.0)</div>
            <br></br>
        </div>
        <div style={{margin:mobile ? "0":"5em 5em 0em 2em"}}>
            <div style={{fontFamily:"Luckiest Guy"}}>DESENVOLVIMENTO DE SOFTWARE</div>
            <div>Diego Prosperi Turibio (verso 1.0)</div>
            <div>Lusa Anastcio (verso 1.0)</div>
            <div>Laura Genari (verso 2.0)</div>
            <br></br>
            <div style={{fontFamily:"Luckiest Guy"}}>CONTEDO E ARTE</div>
            <div>Marlia Faustino da Silva</div>
            <br></br>
            <div style={{fontFamily:"Luckiest Guy"}}>MSICA</div>
            <div> Benjamin Tissot - Buddy</div>
        </div>
    </div>
    return (    
        <div>
            <TemplateEscuro mobile={mobile} id="CRDITOS" font={"Luckiest Guy"} size="2.3em" bolinhas={false }/>
            {content}
        </div>
    )    
    }


